import React from "react"
import PasteDelegate from './PasteDelegate';

class PasteEcho extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return <PasteDelegate>{(state) => {
      return <pre style={{whiteSpace: 'normal'}} dangerouslySetInnerHTML={{__html: state.text}}/>
    }}</PasteDelegate>
  }
}


export default PasteEcho;
